//  BS5 nav-link  padding
.navbar-expand-lg .navbar-nav .nav-link{
    padding: 0;
}
// 8/19 code signin
@include media-breakpoint-up(lg){
    .position-lg-absolute{
        position: absolute;
    }
    .translate-lg-middle{
        transform: translate(-50%, -50%);
    }
}
// swiper
.swiper-container {
    width: 100%;
    height: 100%;
  }
.swiper-img{
    height: 350px;
    width: 350px;
}
//  
.subscribe{
    background-image: url('https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/exhibition-week6/wladislaw-peljuchno-Y_f1aHpR1tc-unsplash.jpg');
    background-position: center center;
    background-size: cover;
    // padding-bottom: 36px;
    height: 400px;
    &-content{
        //posamargin
        margin-bottom: 36px;
    }
}
// cart fixed modal
.cart{
    &-title{
        border-bottom: none;
    }
    &-footer{
        border-top: none;
    }
    &-img{
        height: 120px;
    }
}
.modal-content{
    background-color: $black; 
    overflow-y: auto;  // modal-dialog  modal-dialog-scrollable 
    height: 100%; // modal.content  h-100 
}
.modal.cart .modal-dialog{
    position: fixed;
    top: 0;
    right: 0;
    // bottom: 0;
    // width: 100%;
    height: 100%; // modal 
    margin: 0; // modal 
    transform: translate3d(0%, 0, 0);
}
